const getters={
  // goods
  selectFoods:function (state) {
    let foods=[];
    state.goods.forEach(function (good) {
      good.foods.forEach(function (food) {
        if (food.count){
          foods.push(food);
        }
      })
    });
    return foods;
  }
}


const mutations={
  loadGoods:function (state,goodsData) {
    state.goods = goodsData;
  },
  loadSeller:function (state,sellerData) {
    state.seller = sellerData;
  },
  loadRatings:function (state,ratingsData) {
    state.ratings = ratingsData;
  },

  addCart:function (state,{goodIndex,foodIndex}) {
    state.goods[goodIndex].foods[foodIndex].count++;
  },
  removeCart:function (state,{goodIndex,foodIndex}) {
    state.goods[goodIndex].foods[foodIndex].count--;
  },
  setCart:function (state,{goodIndex,foodIndex}) {
    // goodIndexfoodIndex
    Vue.set(state.goods[goodIndex].foods[foodIndex],"count",1);
    Vue.set(state.goods[goodIndex].foods[foodIndex],"goodIndex",goodIndex);
    Vue.set(state.goods[goodIndex].foods[foodIndex],"foodIndex",foodIndex);
  },
  emptyCart:function (state,selectFoods) {
    selectFoods.forEach(function (selectFood) {
      state.goods[selectFood.goodIndex].foods[selectFood.foodIndex].count=0;
    });
  }
}
const actions={
  getGoods:function ({commit}) {
    return new Promise((resolve,reject)=>{
      Vue.http.get('/api/goods').then( (response) =>{
        response=response.body;
        resolve(response);
        commit('loadGoods',response.data);
      },error=>{
        reject(error);
      });
    });
  },

  getSeller:function ({commit}) {
    return new Promise((resolve,reject)=>{
      Vue.http.get('/api/seller').then( (response) =>{
        response=response.body;
        resolve(response);
        commit('loadSeller',response.data);
      },error=>{
        reject(error);
      });
    });
  },

  getRatings:function ({commit}) {
    return new Promise((resolve,reject)=>{
      Vue.http.get('/api/ratings').then( (response) =>{
        response=response.body;
        resolve(response);
        commit('loadRatings',response.data);
      },error=>{
        reject(error);
      });
    });
  }


  // ***mutationsactions
  // addCart_Action:function ({commit},foodIndex) {
  //   commit('addCart',foodIndex);
  // },
  // removeCart_Action:function ({commit}) {
  //   commit('removeCart');
  // },
  // setCart_Action:function ({commit},foodIndex) {
  //   commit('setCart',foodIndex);
  // }
}


export default new Vuex.Store({
  state,
  getters,
  mutations,
  actions
});



